import {   SIGNIN_FAIL,
    SIGNIN_SUCCESS,
    LOGOUT,
    LOAD_USER,
    LOGIN_LOADING_TRUE,
    UPLOAD_IMAGE,
    LOAD_EDUCATION,LOAD_EXPERIENCE,
    LOAD_SKILLS,LOAD_SUMMARY,
    LOAD_WORK_SAMPLE,PROFILE_LOADING_TRUE,
    PROFILE_LOADING_FALSE,
    UPDATE_SUMMARY,
    ACTIVE_HANDLER,DELETE_EDUCATION
 } from "../ActionsTypes"; 

 import axios from "../../Utillity/axios";

 


export const signUser = (authUser,history) => dispatch => {
    console.log(authUser)
   axios.post('/user/login', authUser)
        .then( success =>{
            return dispatch(successSignin(success.data)), history.push('/profile');
        })
        .catch( fail => {
            history.push('/login');
            return dispatch(failSignin(fail.response));
        });
};

