export const resetpassword = (passwords, history) => dispatch => {
    axios.post('users/resetpassword', passwords)
        .then( success =>{
            history.push('/signin');
            console.log(success);
            return dispatch(logoutUser());
        })
        .catch( fail => {
            console.log(fail.response.data);
            return dispatch(failSignin(fail.response.data)); 
        });
};

